//Default Overrides For Styles In Minima
// If you wish to override any of this CSS, do so in _sass/minima/custom-styles.css

.post img {
  display: block;
  // border:1px solid #021a40;
  vertical-align: top;
  margin-left: auto;
  margin-right: auto;
}

.post figcaption {
  text-align: center;
  font-size: 0.8rem;
  font-style: italic;
  color: light-grey;
}

.page-content {
  -webkit-font-smoothing: antialiased !important;
  text-rendering: optimizeLegibility !important;
}
// make non-headings slightly lighter
.post-content p,
.post-content li {
  font-size: 20px;
  color: #515151;
}

.post-link {
  font-weight: normal;
}

// change padding of headings
h1 {
  margin-top: 2.5rem !important;
}

h2 {
  margin-top: 3rem !important;
}

h3,
h4 {
  margin-top: 1.5rem !important;
}

p {
  margin-top: 1rem !important;
  margin-bottom: 1rem !important;
}

h1,
h2,
h3,
h4 {
  margin-bottom: 0.5rem !important;
}

pre {
  margin-bottom: 1.5rem !important;
}

// make sure the post title doesn't have too much spacing
.post-title {
  margin-top: 0.5rem !important;
}

li {
  h3,
  h4 {
    margin-top: 0.05rem !important;
    margin-bottom: 0.05rem !important;
  }
  .post-meta-description {
    color: rgb(88, 88, 88);
    font-size: 15px;
    margin-top: 0.05rem !important;
    margin-bottom: 0.05rem !important;
  }
}

//  Code Folding
details.description[open] summary::after {
  content: attr(data-open);
}

details.description:not([open]) summary::after {
  content: attr(data-close);
}

// Notebook badges
.notebook-badge-image {
  border: 0 !important;
}

// Adjust font size for footnotes.
.footnotes {
  font-size: 12px !important;
  p,
  li {
    font-size: 12px !important;
  }
}

// Adjust with of social media icons were getting cut off
.social-media-list {
  .svg-icon {
    width: 25px !important;
    height: 23px !important;
  }
}

// Make Anchor Links Appear Only on Hover

.anchor-link {
  opacity: 0;
  padding-left: 0.375em;
  \-webkit-text-stroke: 1.75px white;
  \-webkit-transition: opacity 0.2s ease-in-out 0.1s;
  \-moz-transition: opacity 0.2s ease-in-out 0.1s;
  \-ms-transition: opacity 0.2s ease-in-out 0.1s;
}

h1:hover .anchor-link,
h2:hover .anchor-link,
h3:hover .anchor-link,
h4:hover .anchor-link,
h5:hover .anchor-link,
h6:hover .anchor-link {
  opacity: 1;
}

// category tags
.category-tags {
  margin-top: 0.25rem !important;
  margin-bottom: 0.25rem !important;
  font-size: 105%;
}

// Custom styling for homepage post previews
.post-meta-title,
.post-meta {
  margin-top: 0.25em !important;
  margin-bottom: 0.25em !important;
  font-size: 105%;
}

.page-description {
  margin-top: 0.5rem !important;
  margin-bottom: 0.5rem !important;
  color: #585858;
  font-size: 115%;
}

// Custom styling for category tags
.category-tags-icon {
  font-size: 75% !important;
  padding-left: 0.375em;
  opacity: 35%;
}
.category-tags-link {
  color: rgb(187, 129, 129);
  font-size: 13px;
}

// Search Page Styles
.js-search-results {
  padding-top: 0.2rem;
}
.search-results-list-item {
  padding-bottom: 1rem;
}
.search-results-list-item .search-result-title {
  font-size: 16px;
  color: #d9230f;
}
.search-result-rel-url {
  color: silver;
}
.search-results-list-item a {
  display: block;
  color: #777;
}
.search-results-list-item a:hover,
.search-results-list-item a:focus {
  text-decoration: none;
}
.search-results-list-item a:hover .search-result-title {
  text-decoration: underline;
}

.search-result-rel-date {
  color: rgb(109, 120, 138);
  font-size: 14px;
}

.search-result-preview {
  color: #777;
  font-size: 16px;
  margin-top: 0.02rem !important;
  margin-bottom: 0.02rem !important;
}
.search-result-highlight {
  color: #2e0137;
  font-weight: bold;
}

// Handle Overflow With Table Output

table {
  display: block !important;
  overflow-x: auto;
  white-space: nowrap;
  font-size: 75%;
  border: none;
  th {
    text-align: center !important;
  }
  td {
    text-overflow: ellipsis;
    overflow: hidden;
    max-width: 15em;
  }
}

// customize scrollbars
::-webkit-scrollbar {
  width: 14px;
  height: 18px;
}
::-webkit-scrollbar-thumb {
  height: 6px;
  border: 4px solid rgba(0, 0, 0, 0);
  background-clip: padding-box;
  -webkit-border-radius: 7px;
  background-color: #9d9d9d;
  -webkit-box-shadow: inset -1px -1px 0px rgba(0, 0, 0, 0.05),
    inset 1px 1px 0px rgba(0, 0, 0, 0.05);
}
::-webkit-scrollbar-button {
  width: 0;
  height: 0;
  display: none;
}
::-webkit-scrollbar-corner {
  background-color: transparent;
}

// Wrap text outputs instead of horizontal scroll
.output_text.output_execute_result {
  pre {
    white-space: pre-wrap;
  }
}
